/* ==========================================================================
   Portfolio Slider shortcode style - begin
   ========================================================================== */

.mkd-portfolio-slider-holder {
    @include mkdRelativeHolderLayout();
	opacity: 0;

	.mkd-pl-inner {
		margin: 0 !important;
	}
	
	article {
		width: auto !important;
		padding: 0 !important;
		margin: 0 !important;
		float: none !important;
	}

	.owl-stage-outer {
		position: relative;
		overflow: hidden;
		@include mkdTransform(translate3d(0, 0, 0));
	}

	.owl-item {
		position: relative;
		min-height: 1px;
		float: left;
	}

	.owl-nav {

		&.disabled {
			display: none;
		}

		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			@include mkdTransform(translateY(-50%));
			
			&:hover {
				
				.mkd-prev-icon,
				.mkd-next-icon {
					color: $first-main-color;
				}
			}
		}
		
		.owl-prev {
			left: 20px;
		}
		
		.owl-next {
			right: 20px;
		}
		
		.mkd-prev-icon,
		.mkd-next-icon {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			color: $default-heading-color;
			line-height: 1;
			@include mkdTransition(color .2s ease-out);
			
			.mkd-icon-arrow {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 46px;
				line-height: inherit;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
	}
	
	.owl-dots {
		position: absolute;
		left: 0;
		bottom: 20px;
		width: 100%;
		text-align: center;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 12px;
				height: 12px;
				margin: 0 6px;
				border-radius: 3em;
				background-color: transparent;
				border: 2px solid rgba($default-heading-color, .5);
				@include mkdTransition(background-color .2s ease-out, border .2s ease-out);
				box-sizing: border-box;
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: $default-heading-color;
					border-color: $default-heading-color;
				}
			}
		}
	}

	.mkd-portfolio-list-holder {

		&.mkd-nav-light-skin {

			.owl-nav {

				.owl-prev,
				.owl-next {

					&:hover {

						.mkd-prev-icon,
						.mkd-next-icon {
							color: $header-light-hover-color;
						}
					}
				}

				.mkd-prev-icon,
				.mkd-next-icon {
					color: $header-light-color;
				}
			}
		}

		&.mkd-nav-dark-skin {

			.owl-nav {

				.owl-prev,
				.owl-next {

					&:hover {

						.mkd-prev-icon,
						.mkd-next-icon {
							color: $header-dark-hover-color;
						}
					}
				}

				.mkd-prev-icon,
				.mkd-next-icon {
					color: $header-dark-color;
				}
			}
		}

		&.mkd-pag-light-skin {

			.owl-dots {

				.owl-dot {

					span {
						background-color: rgba($header-light-color, .2);
					}

					&.active,
					&:hover {

						span {
							background-color: $header-light-hover-color;
						}
					}
				}
			}
		}

		&.mkd-pag-dark-skin {

			.owl-dots {

				.owl-dot {

					span {
						background-color: rgba($header-dark-color, .2);
					}

					&.active,
					&:hover {

						span {
							background-color: $header-dark-hover-color;
						}
					}
				}
			}
		}

		&.mkd-pag-bellow-slider {

			.owl-nav {

				.owl-prev,
				.owl-next {
					margin: -19px 0 0;
				}
			}

			.owl-dots {
				position: relative;
				bottom: auto;
				margin: 16px 0 0;
			}
		}
	}
}
/* ==========================================================================
   Portfolio Slider shortcode style - end
   ========================================================================== */